import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';


import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';

import { MessageService } from "primeng/components/common/messageservice";
import { Customtools } from "app/pages/tzb/custom/http/customUtil";
import { ConfirmationService } from 'primeng/primeng';
import { ActivatedRoute } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { tableMessage } from "app/@uisftech/common/constant/config";
import { Commfunc } from "app/@uisftech/common/service/commonFun";

@Component({
  selector: 'ec-eeclist',
  templateUrl: './ec-eeclist.component.html',
  styleUrls: ['./ec-eeclist.component.scss'],
  providers: []
})
export class EcEeclistComponent implements OnInit {
  @Input() index: any;
  @Output() outValueEmit: EventEmitter<any> = new EventEmitter();

  ch: any = CALENDAR_CN;

  selectedValues: any;

  selectDecisionForm: FormGroup;  // 新增表单

  tabTable: any;

  loadingShow: any = { flag: false }; // loading框

  pageSize: number = 10;
  pageNum: number = 1;
  totalNum: number;
  first: number = 0;

  LogicDesc: any = [
    { label: '且', value: '01' },
    { label: '或', value: '02' },
    { label: '自定义', value: '03' }
  ]

  personPage:boolean = false; // 选择创建人弹窗

  createorId:string;  // 当作参数的创建人ID

  selectedCar:any;  // 选中的策略

  tableMesg: any = tableMessage;

  constructor(
    private fb: FormBuilder,
    private httpService: CusOperationHttpService,
    private messageService: MessageService,
    private commonFunc: Customtools,
    public commfunc: Commfunc,
  ) {

  }
  ngOnInit() {
    this.selectDecisionForm = this.fb.group({
      'strategyName': [''],
      'createUserId': [''],
      'startDate': [''],
      'endDate': [''],
    });

    this.queryData();


  }

  // 点击查询
  query() {
    this.first = 0;
    this.queryData();
  }
  // 重置
  reset() {
    this.selectDecisionForm.reset();
    this.createorId = '';
    // this.queryData();
  }
  // 分页
  paginate(event) {
    // 每页显示的条数;
    this.pageSize = event.rows;
    // 当前页
    this.pageNum = event.page + 1;
    this.first = event.first;
    // 调用查询的方法
    this.queryData();
  }

  // 默认查询事件
  queryData() {
    const params = {};
    params['businessId'] = 'C0001'
    params['pageNum'] = this.pageNum,// 当前页码
    params['pageSize'] = this.pageSize// 页面显示几条
    params['flag'] = '0' // 标志位  查询所有已经创建的策略
    for (var key in this.selectDecisionForm.value) {
      if (this.selectDecisionForm.value[key]) {
        params[key] = this.selectDecisionForm.value[key]   
      }
    }
    if(this.createorId){
      params['createUserId'] = this.createorId;
    }
    params['startDate']?params['startDate'] = this.commfunc.transDateN(params['startDate']):params['startDate'] = '';
    params['endDate']?params['endDate'] = this.commfunc.transDateN(params['endDate']):params['endDate'] = '';
   
    const configParam = {
      httpService: this.httpService,
      loadingFlag: true,
      messageFlag: false,
      inputParam: params,
      message: this.messageService,
      injectName: 'queryStreamStrategyConfig',
      loadingShow: this.loadingShow,
      messageInfo: '查询成功',
    }
    this.commonFunc.haddleData(configParam, (bodyData) => {
      debugger;
      if (bodyData) {

        if (bodyData.strategyList) {
          bodyData.strategyList.forEach(element => {
            element['strategyTypeDesc']  = "SQL";
          });
        }

        debugger;
        this.tabTable = bodyData.strategyList;
        this.totalNum = bodyData.totalNum;
      }
    });
  }

  // 选择创建人
  selectCreateor(){
    this.personPage = true;
  }

// 选择创建人之后的回调函数
  workerCall(event){
    this.personPage = false;
    if(event.length > 0){
      this.selectDecisionForm.controls['createUserId'].setValue(event[0].tellerName)
      this.createorId = event[0].tellerId;
    }
    
  }


  back() {
    this.outValueEmit.emit(false);
  }

  submit(event){
    this.outValueEmit.emit(event);
  }

}